div nav.navbar
{
    height: 100%;
    width: 200px;
    position: fixed;
    top: 10px;
    overflow: auto;
    /*background-color: rgb(55, 95, 130);*/
}

div nav.navbar a
{
    font-family: "Open Sans", sans-serif;
    display: block;
    /*color: gold;*/
    color: rgb(238,240,242);
    padding: 8px 16px;
    text-decoration: none;
    float: none;
}

div nav.navbar a.active::before
{
    content: "\25B8\2009";
}
/* Marks current page with different color + underline */
div nav.navbar a.active
{
    font-weight: bold;
    text-decoration: none;
}
div nav.navbar a.active::after
{
    content: "\2009\25C2";
}

/* Marks page hovered with mouse */
div nav.navbar a:hover
{
    text-decoration: underline;
}

/* Styling for responsive page icon */
div nav.navbar a.icon
{
    display: none;
    text-decoration: none;
    font-weight: bolder;
    float: right;
    height: auto;
    margin: 10px;
    border: solid 0px black;
    border-radius: 10px;
    color: rgb(210, 220, 230);
}

/* Applies to mobile links:
When screen size < 600px, hide all links except "Home" and expand icon
*/
@media screen and (max-width: 700px)
{
    div nav.navbar div.hide-mobile
    {
        display: none;
    }
    /* Applies to mobile navbar when not clicked */
    div nav.navbar
    {
        width: 100%;
        height: auto;
        margin: 10px 0;
        top: auto;
        position: static;
    }
    div nav.navbar a
    {
        padding: 8px 16px;
    }
    div nav.navbar a:not(:first-child)
    {
        display: none;
    }
    div nav.navbar a.active
    {
        text-decoration: none;
        display: block;
    }
    div nav.navbar a.icon
    {
        display: block;
        float: none;
        text-align: center;
        position: absolute;
        top: 0;
        right: 0;
        height: auto;
        width: auto;
        background-color: transparent;
        -webkit-transition: 0.5s;
        transition: 0.5s;
    }

    div nav.navbar a.icon:hover
    {
        background-color: rgb(60, 60, 60);
        color: rgb(200, 205, 210);

    }

    div nav.navbar.responsive
    {
        
        -webkit-transition:200ms ease;
        transition:200ms ease;
    }
    
    /* Applies to responsive navbar when clicked */
    div nav.navbar.responsive a 
    {
        display: block;
        text-align: left;
        /*margin-left: 45px;*/
        margin-left: 0;
    }

    div nav.navbar.responsive a.icon
    {
        background-color: rgb(60, 60, 60);
        color: rgb(200, 205, 210);
    }
}

/* @import url(//fonts.googleapis.com/css?family=Noto+Serif); */
/* OpenSans font is imported from 'styles_navbar.css' */

/* Default styling for all pages */
html
{
    font-family: "Open Sans", sans-serif;
    /*background-color: rgb(55, 95, 130);*/
    background-color: rgb(40,40,40);
    /* overflow: scroll; */
    font-size: 100%;
}

/*header.name, div.profile, footer*/
header.name, article, footer
{
    background: white;
}

/* Applies to name/image/link headings on every page
Is not visible in mobile devices (hidden)
*/
header.name
{
    text-align: right;
    padding: 16px 32px 16px 32px;
    margin: 20px;
    margin-left: 200px;
}

article, hr
{
    font-family: "Open Sans", sans-serif;
    /* text-align: right; */
    /* padding: 8px 32px 8px 32px; */
    /* overflow: auto; */
    padding: 16px 32px 16px 32px;
    margin: 20px;
    /* margin-right: auto;
    margin-left: 200px; */
    margin-left: 200px;
}

h1, h2, h3, h4, h5, h6, a
{
    font-family: "Open Sans", sans-serif;
    color: rgb(29, 31, 33);
}

/* Applies to h2 subheading - is underlined for emphasis */
h2
{
    text-decoration: underline;
    margin-left: 20px;
}

