/*CSS for navbar moved to different file*/

/*Dividing lines - sub-lines are overridden by CSS after*/
hr
{
    border-width: 4px;
    display: none;
}
/* Sub dividing-lines are thinner & less apparent */
hr.sub
{
    border-width: 1px;
    margin-left: 20px;
    margin-right: 20px;
}

/*Change list icon to square, from standard circle*/
li
{
    list-style-type: square
}

/* Applies to footer of page - only creates padding for ease in readability
Unlike other general contents in page, text is aligned to center instead of left
*/
footer
{
    font-family: "Open Sans", sans-serif;
    text-align: center;
    padding: 8px 32px 8px 32px;
    margin: 10px;
    margin-right: auto;
    margin-left: 200px;
}

/* General settings for mobile devices' page, excluding navbar (see other CSS sheet)
* Causes margin on top of regular page to expand from 20px to 40px
* Moves image from float-right to center
* Excluding index.html (which has an overriding style), hides name/links/image header entirely
*/
@media screen and (max-width: 875px)
{
    header.name
    {
        text-align: center;
    }
    /*img.profile
    {
        float: none;
        margin-left: auto;
        margin-right: auto;
        height: 200px;
    }*/
}

@media screen and (max-width: 700px)
{
    header.name
    {
        display: none;
        margin-top: 0;
        margin-left: 0;
    }
    article
    {
        margin-top: 0;
        margin-left: 0;
    }

    footer
    {
        margin-left: 0;
    }
}

/* FontAwesome icons */
a i.fab
{
    font-size: 25px;
    color: dimgrey;
}

/* Applies to blog page
* Creates dividing lines between articles & years
*/
section.blog section.year::after
{
    content: "";
    display: block;
    border-style: inset;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    border-width: 4px;
    margin-left: auto;
    margin-right: auto;
}
section.blog section.year section.month::after
{
    content: "";
    display: block;
    border-style: inset;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    border-width: 2px;
    margin-left: 20px;
    margin-right: 20px;
}
section.blog section.year section.month article::after
{
    content: "";
    display: block;
    border-style: inset;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
    border-width: 1px;
    margin-left: 20px;
    margin-right: 20px;
}

article, section
{
    page-break-inside: avoid;
}
@media screen
{
    .no-print
    {
        display: inline;
    }
    
    .print-only
    {
        display: none;
    }
}

@media print
{    
    html
    {
        line-height: 16px;
        font-family: "Open Sans", sans-serif;
    }
    div nav.navbar
    {
        display: none; /* !important;*/
    }
    
    header.name, article, p
    {
        margin: 0;
        padding: 0;
        overflow:hidden !important;
    }
    
    article.achievements ul
    {
        margin: 0;
        list-style-type: square;
    }
    
    header.name p.links, hr.sub
    {
        display: none;
    }
    
    header.name h1
    {
        font-size: 16px;
    }
    
    header.name
    {
        text-align: center;
    }
    
    h1, h2, h3, h4, h5, h6, section, p
    {
        font-size: 13px;
        padding: 0;
    }
    
    section h3
    {
        text-decoration: underline;
    }
    
    hr
    {
        border-width: 1px;
    }
    
    hr.sub
    {
        margin-left: 40px;
        margin-right: 40px;
    }
    
    footer
    {
        display: none;
    }
    
    .no-print
    {
        display: none;
    }
    
    .print-only
    {
        display: inline;
    }
}
